import React from 'react';

class Video extends React.Component{
    handleClick=()=>{
        this.props.grab_video_details(this.props.the_video)
        console.log(this.props.the_video.id.videoId)
    }
    render(){
        return(
            <div className='VideoSide' onClick={this.handleClick} >
                <img src={this.props.link} />
                <h4>{this.props.title}</h4>
            </div>
        )
    }
}

export default Video